import React, { FC } from 'react';
import { ForWhoElementProps } from '../forWho.types';

const Commision: FC<ForWhoElementProps> = ({ className: classes }) => (
  <div className={classes}>
    <h3 className="forWho__commision--title">Czym jest prowizja bankowa?</h3>
    <p className="forWho__commision--desc">
      <span className="bold">Prowizja to istotny </span>
      parametr przekadajcy si na cakowity koszt kredytu. Jest to opata pobierana przez bank tytuem udzielenia
      finansowania. Ma ona charakter jednorazowy i jest wyliczana jako procent od przyznanej kwoty kredytu.
    </p>
    <p className="forWho__commision--desc">
      <span className="bold">Prowizje od kredytw i poyczek gotwkowych </span>
      udzielanych przez banki s z roku na rok coraz wysze.
      <br />
      Klientw nie powinno ju dziwi, gdy prowizja dochodzi do kilkunastu, a w skrajnych przypadkach nawet{' '}
      <span className="bold">do 30%.</span>
    </p>
    <p className="forWho__commision--desc">
      Jeeli spacili Pastwo kredyt przed czasem, skonsolidowali lub przenieli do innego banku, to zgodnie ze
      stanowiskiem rzecznika finansowego i prezesa UOKiK, bank <span className="bold">powinien odda Pastwu</span> t
      cz prowizji, ktrej klient nie spaci w ratach.
    </p>
    <p className="forWho__commision--desc">
      Niestety <span className="bold">ponad 95% osb, nawet nie wie</span>
      , e ma do tego prawo,
      <br />
      a bank dobrowolnie nie zwrci takiej prowizji.
      <br />
      Klientowi zostaje jedynie droga sdowa.
    </p>
  </div>
);

export default Commision;
