import React, { FC } from 'react';
import './header.scss';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faArrowDown } from '@fortawesome/free-solid-svg-icons';

const Header: FC = () => (
  <header id="header" className="header">
    <div className="header__main">
      <h2 className="header__main__text header__main__text--first">
        Czy Twj bank <br /> poinformowa Ci, <br /> e naley Ci si{' '}
        <span className="header__main__text--important">zwrot prowizji</span> z kredytu gotwkowego?
      </h2>
      <p className="header__main__text header__main__text--second">
        Jeeli spacie przed terminem, skonsolidowae lub przeniose do innego banku swj kredyt to uzyskamy dla
        Ciebie gwarantowany zwrot prowizji!
      </p>
      <div className="header__main__text header__main__text--third">
        <h3 className="h3">Nawet 10 000 z!</h3>
        <a href="#contact">
          <button className="header__button">Zamw darmow analiz</button>
        </a>
      </div>
    </div>
    <div className="header__animationArrow">
      <FontAwesomeIcon icon={faArrowDown} />
    </div>
  </header>
);

export default Header;
