import React, { FC } from 'react';
import './practice.scss';
const IconFile = require('../../images/practice-icons/file-1.png');
const IconSupport = require('../../images/practice-icons/support.png');
const IconNotepad = require('../../images/practice-icons/notepad-1.png');
const IconMoney = require('../../images/practice-icons/notes.png');

interface PraticeViewProps {
  titleClass: string;
  elementClass: string;
}

const PracticeView: FC<PraticeViewProps> = ({ titleClass, elementClass }) => {
  const leftElementClass = `${elementClass} element--left`;
  const rightElementClass = `${elementClass} element--right`;

  return (
    <section id="practice" className="practice">
      <h2 className={titleClass}>Jak wyglda cay proces?</h2>
      <div className="practice__main">
        <div className={leftElementClass}>
          <div className="element__image">
            <img src={IconNotepad} alt="" />
          </div>
          <div className="element__desc">
            <h3 className="element__desc--title">Wypenij formularz kontaktowy</h3>
            <p className="element__desc--text">
              Skontaktuj si z nami za pomoc formularza kontaktowego, dostpnego na dole tej strony.
            </p>
          </div>
        </div>
        <div className={rightElementClass}>
          <div className="element__image">
            <img src={IconSupport} alt="" />
          </div>
          <div className="element__desc">
            <h3 className="element__desc--title">Rozmowa z naszym konsultantem</h3>
            <p className="element__desc--text">
              Na tym etapie bdziemy potrzebowali informacji na temat kredytw, ktre posiadae. Nasz konsultant
              wyjani Ci dosownie wszystko i odpowie na kade pytanie.
            </p>
          </div>
        </div>
        <div className={leftElementClass}>
          <div className="element__image">
            <img src={IconFile} alt="" />
          </div>
          <div className="element__desc">
            <h3 className="element__desc--title">Przesanie dokumentw i analiza</h3>
            <p className="element__desc--text">
              Na podstawie przesanych do nas drog mailow dokumentw, nasi pracownicy dokonaj dokadnej analizy oraz
              oblicz ile przysuguje Ci zwrotu.
              <span className="element__desc--text--extra">
                Jeszcze tego samego dnia poznasz ostateczn propozycj z wycen!
              </span>
            </p>
          </div>
        </div>
        <div className={rightElementClass}>
          <div className="element__image">
            <img src={IconMoney} alt="" />
          </div>
          <div className="element__desc">
            <h3 className="element__desc--title">Podpisanie umowy i wypata</h3>
            <p className="element__desc--text">
              <span className="element__desc--text--multitext">
                Po akceptacji kwoty odkupu wierzytelnoci, wygenerowane dokumenty wysyamy do Ciebie na e-mail.
              </span>
              <span className="element__desc--text--multitext">
                Nastpnie dokumenty naley wydrukowa i wysa do nas poczt. Gdy tylko otrzymamy podpisan umow,
                zlecamy wypat rodkw.
              </span>
              <span className="element__desc--text--multitext">
                Pienidze trafiaj do Ciebie w cigu 24 godzin od momentu otrzymania podpisanych dokumentw.
              </span>
            </p>
          </div>
        </div>
      </div>
    </section>
  );
};

export default PracticeView;
