import React, { ChangeEvent, FC, SyntheticEvent } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faCaretRight } from '@fortawesome/free-solid-svg-icons';

interface ContactFormProps {
  showFalseName: boolean;
  showFalseEmail: boolean;
  showFalseAgreements: boolean;
  handleSubmit: (e: SyntheticEvent<HTMLButtonElement>) => void;
  handleInputChange: (e: ChangeEvent<HTMLInputElement>) => void;
  handleCheckboxInputChange: (e: ChangeEvent<HTMLInputElement>) => void;
}

const ContactFormView: FC<ContactFormProps> = ({
  showFalseAgreements,
  showFalseEmail,
  showFalseName,
  handleInputChange,
  handleSubmit,
  handleCheckboxInputChange,
}) => (
  <div className="contact__form">
    <div className="contact__form__wrap">
      <h2 className="contact__form--title">Zamw darmow analiz</h2>
      <div className="contact__form--main">
        <form className="contactForm" action="post" name="contactForm">
          <div className="contactForm__inputWrap">
            <input onChange={handleInputChange} type="text" placeholder="Imi" id="formName" className="contactInput" />
            <label htmlFor="formName"></label>
          </div>
          <div className="contactForm__inputWrap">
            <input
              onChange={handleInputChange}
              type="text"
              placeholder="Nazwisko"
              id="formSurname"
              className="contactInput"
            />
            <label htmlFor="formSurname"></label>
          </div>
          <div className="contactForm__inputWrap">
            <input
              onChange={handleInputChange}
              type="email"
              placeholder="Adres e-mail"
              id="formEmail"
              className="contactInput"
            />
            <label htmlFor="formEmail"></label>
          </div>
          <div className="contactForm__inputWrap">
            <input
              onChange={handleInputChange}
              type="number"
              placeholder="Numer telefonu"
              id="formPhone"
              className="contactInput"
            />
            <label htmlFor="formPhone"></label>
          </div>
          {showFalseName ? <div className="contactForm__false falseName">Imi jest wymagane.</div> : null}
          {showFalseEmail ? <div className="contactForm__false falseEmail">Email jest wymagany.</div> : null}
          <div className="contactForm__inputWrap--checkbox">
            <input
              onChange={handleCheckboxInputChange}
              type="checkbox"
              id="inputPrivatePolicy"
              className="checkbox__input"
            />
            <label htmlFor="inputPrivatePolicy" className="checkbox__label">
              Owiadczam, i zapoznaem si z{' '}
              <a className="contactForm__link" aria-label="Polityka Prywatnoci" target="_blank" href="/privatePolicy">
                polityk prywatnoci
              </a>{' '}
              serwisu oraz akceptuj jej postanowienia.
            </label>
          </div>
          <div className="contactForm__inputWrap--checkbox">
            <input onChange={handleCheckboxInputChange} type="checkbox" id="inputRODO" className="checkbox__input" />
            <label htmlFor="inputRODO" className="checkbox__label">
              Wyraam zgod na przesyanie informacji handlowych za pomoc rodkw komunikacji elektronicznej w
              rozumieniu ustawy z dnia 18 lipca 2002 roku o wiadczenie usug drog elektroniczn (Dz.U.2017.1219 t.j.)
              na podany adres e-mail na temat usug oferowanych przez Jakuba Taskiego zamieszkaego migiel, ul.
              Mierosawskiego 1 64-030. Zgoda jest dobrowolna i moe by w kadej chwili wycofana, klikajc w odpowiedni
              link na kocu wiadomoci e-mail. Wycofanie zgody nie wpywa na zgodno z prawem przetwarzania, ktrego
              dokonano na podstawie zgody przez jej wycofaniem.
            </label>
          </div>
          {showFalseAgreements ? <div className="contactForm__false falseAgreement">Zgody s wymagane.</div> : null}
          <button onClick={handleSubmit} type="submit" className="contactForm__button">
            Zamw darmow analiz nalenego Tobie zwrotu <FontAwesomeIcon icon={faCaretRight}></FontAwesomeIcon>
          </button>
        </form>
      </div>
    </div>
  </div>
);

export default ContactFormView;
