import React, { FC } from 'react';
import { Props } from '../ForWho';

const About: FC<Props> = ({ className }) => (
  <div className={className}>
    <h2 className="forWho__about--title">Gwarancja</h2>
    <p className="forWho__about--desc">
      Jestemy jedyn kancelari w kraju, ktra specjalizuje si w uzyskiwaniu
      <br />
      gwarantowanego zwrotu prowizji bankowej z kredytw konsumenckich w zaledwie kilka dni!
      <br />
      <br />
      Zaufao Nam ponad 1000 zadowolonych klientw z caej Polski. Docz do Nich ju dzi!
    </p>
  </div>
);

export default About;
