import React, { FC } from 'react';
import './aboutUs.scss';

interface AboutUsViewProps {
  titleClass: string;
  descClass: string;
  numberOfMoney: number;
}

const AboutUsView: FC<AboutUsViewProps> = ({ titleClass, descClass, numberOfMoney }) => {
  const moneyDescClass = `${descClass} about__main--desc-money`;
  const underlineDescClass = `${descClass} underline`;

  return (
    <section className="about">
      <div className="about__image"></div>
      <div className="about__main">
        <h2 id="aboutUs" className={titleClass}>
          Czym si zajmujemy?
        </h2>
        <p className={descClass}>
          Nasza firma zajmuje si <span className="bold">odzyskiwaniem prowizji bankowych</span> z kredytw
          konsumenckich. <br /> Oferta dotyczy
          <span className="underline"> odkupienia praw do rozlicze finansowych </span> umw kredytw/poyczek.
        </p>
        <p className={descClass}>
          Dziki takiemu rozwizaniu klient otrzymuje pienidze na swoje konto
          <span className="bold"> do 7 dni od pierwszego kontaktu</span> z nami, <br /> bez koniecznoci czekania wielu
          miesicy, a nawet lat na rozstrzygnicie sprawy w sdzie.
        </p>
        <p className={moneyDescClass}>
          <span className="numbers">{numberOfMoney}</span> PLN
        </p>
        <p className={underlineDescClass}> Nawet tak kwot wypacimy Ci w 24h.</p>
        <a href="#contact">
          <button className="about__main--button">Zamw darmow analiz Twojego zwrotu</button>
        </a>
      </div>
    </section>
  );
};

export default AboutUsView;
